import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
import axios from "axios";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var previous = require("../../../assets/Previous.png");

var FinancialAccount = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState(["All", "Tshirt", "Mug"]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [productsType, setProductsType] = useState("All");
  const [rawProductsState, setRawProducts] = useState([]);
  const [products, setProducts] = useState([]);
  const [search, setSearch] = useState("");
  const [isView, setIsView] = useState(false);
  const status = config.status;

  var getProducts = () => {
    setLoading(true);
    try {
      axios
        .get(config.host + "/api/mocks/GetAllProducts/?token=" + config.token)
        .then((response) => {
          var rawProducts = response.data;
          console.log(rawProducts);
          setRawProducts(rawProducts);
          structureProducts(rawProducts);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var structureProducts = (rawProducts) => {
    var filteredProducts = [];
    for (var i = 0; i < rawProducts.length; i = i + 1) {
      if (
        rawProducts[i].productName.includes(search) &&
        (rawProducts[i].productStatus == productsType || productsType == "All")
      ) {
        filteredProducts.push(rawProducts[i]);
      }
    }

    var structuredProducts = [];
    var productsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredProducts.length; i = i + 1) {
      if (i % 6 == 0) {
        productsRow = { lineId: row, data: [] };
      }
      productsRow.data[i % 6] = filteredProducts[i];
      if (i % 6 == 5) {
        structuredProducts[row] = productsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredProducts[row] = productsRow;
    }
    setProducts(structuredProducts);
    console.log(structuredProducts);

    setLoading(false);
    unsubscribe();
  };

  useEffect(() => {
    structureProducts(rawProductsState);
  }, [search, productsType]);

  useEffect(getProducts, []);
  const unsubscribe = navigation.addListener("didFocus", () => {
    getProducts();
  });

  var createProduct = () => {
    setIsView(true);
  };
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: windowWidth,
          height: windowHeight,
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>

          <Image
            source={logo}
            style={{ width: "15%", height: "80%" }}
            resizeMode={"contain"}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={config.status > 1 ? false : true}
              notifications={[]}
              navigation={navigation}
            />
          </View>
        </View>
        <View
          style={{
            height: "89%",
            width: "100%",
            alignItems: "center",
            alignSelf: "center",
          }}
        >
          <View style={{ height: "2%", flexDirection: "row", width: "100%" }}>
            <TouchableOpacity
              style={{ width: "3%", height: 25, alignSelf: "flex-start" }}
              onPress={() => {
                navigation.pop();
              }}
            >
              <Image
                source={previous}
                style={{
                  width: windowHeight <= 1080 ? 60 : 80,
                  height: windowHeight <= 1080 ? 40 : 50,
                  resizeMode: "contain",
                  marginLeft: 20,
                }}
              />
            </TouchableOpacity>
            <View
              style={{
                width: "20%",
                alignSelf: "center",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                  alignSelf: "center",
                  marginBottom: "1%",
                  marginTop: "2%",
                }}
              >
                Financial account
              </Text>
            </View>
          </View>
          <View style={{ height: "5%" }} />
          <View
            style={{
              width: "90%",
              height: "80%",
              backgroundColor: "#3D9EBC",
              alignItems: "center",
              alignSelf: "center",
              justifyContent: "center",
              borderRadius: 20,
            }}
          ></View>
        </View>
        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default FinancialAccount;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignSelf: "center",
    justifyContent: "flex-start",
    alignItems: "center",
    borderRadius: 4,
    width: "15%",
    height: "4%",
    padding: 8,
    backgroundColor: "#3D9EBC",
    zIndex: 999,
    shadowColor: "#3D9EBC",

    flexDirection: "row",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
