import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import { ScrollView } from "react-native-gesture-handler";
import axios from "axios";
import config from "../../../config";
import Slider from "@react-native-community/slider";
import FlatItem from "./reigon";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

const hyp = Math.sqrt(Math.pow(windowHeight, 2) + Math.pow(windowWidth, 2));

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var notification = require("../../../assets/notification.png");
var placeholder = require("../../../assets/placeholder.png");
var down = require("../../../assets/down-arrow.png");
var magnify = require("../../../assets/magnify.png");
var checked = require("../../../assets/checked.png");
var unchecked = require("../../../assets/unchecked.png");
var no_products = require("../../../assets/Torso.png");

var check = {};
var EditProductMain = ({
  route,
  navigation,
  setIsview,
  updateProducts,
  editProduct,
  setEditProduct,
}) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  const [imageurl, setImageURL] = useState(null);
  const [search, setSearch] = useState("");
  const [designSearch, setDesignSearch] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [products, setProducts] = useState([]);
  const [rawProductsState, setRawProducts] = useState([]);
  const [rawMockupsState, setRawMockups] = useState([]);
  const [searchedProducts, setSearchedProducts] = useState([]);
  var [checklist, setChecklist] = useState({});
  const [designs, setDesigns] = useState([]);
  const [rawDesignsState, setRawDesigns] = useState([]);
  const [step, setStep] = useState(1);
  const [pickedDesign, setPickedDesign] = useState({});
  const [angle, setAngle] = useState("20");
  const [wrapping, setWrapping] = useState("50");

  const [previewImage, setPreviewImage] = useState(null);
  const [mockupImage, setMockupImage] = useState(null);
  const [itemHeight, setItemHeight] = useState(100);
  const [itemWidth, setItemWidth] = useState(100);
  const [reduction, setReduction] = useState("50");
  const [coords, setCoords] = useState({ x: 0, y: 0 });
  const [pattern, setPattern] = useState("t");

  const handleMouseMove = (event) => {
    setCoords({
      x: parseInt(event.nativeEvent.layerX),
      y: parseInt(event.nativeEvent.layerY),
    });
  };

  var getDesigns = () => {
    setLoading(true);
    try {
      axios
        .get(config.host + "/api/mocks/GetAllArtFiles/?token=" + config.token)
        .then((response) => {
          var rawDesigns = response.data;
          console.log(rawDesigns);

          setRawDesigns(rawDesigns);
          structureDesigns(rawDesigns);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var structureDesigns = (rawDesigns) => {
    var filteredDesigns = [];
    for (var i = 0; i < rawDesigns.length; i = i + 1) {
      if (rawDesigns[i].designNameEN.includes(designSearch)) {
        filteredDesigns.push(rawDesigns[i]);
      }
    }

    var structuredDesigns = [];
    var designsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredDesigns.length; i = i + 1) {
      if (i % 6 == 0) {
        designsRow = { lineId: row, data: [] };
      }
      designsRow.data[i % 6] = filteredDesigns[i];
      if (i % 6 == 5) {
        structuredDesigns[row] = designsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredDesigns[row] = designsRow;
    }
    setDesigns(structuredDesigns);
    console.log(structuredDesigns);

    setLoading(false);
  };

  var structureMockups = (rawMockups) => {
    var filteredMockups = [];
    let updateCategories = ["All"];
    for (var i = 0; i < rawMockups.length; i = i + 1) {
      if (
        rawMockups[i].productName.includes(search) &&
        (rawMockups[i].sub_category == selectedCategory ||
          selectedCategory == "")
      ) {
        filteredMockups.push(rawMockups[i]);
      }
      if (!updateCategories.includes(rawMockups[i].category)) {
        updateCategories.push(rawMockups[i].sub_category);
      }
    }

    setCategories(updateCategories);

    var structuredMockups = [];
    var MockupsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredMockups.length; i = i + 1) {
      if (i % 6 == 0) {
        MockupsRow = { lineId: row, data: [] };
      }
      MockupsRow.data[i % 6] = filteredMockups[i];
      if (i % 6 == 5) {
        structuredMockups[row] = MockupsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredMockups[row] = MockupsRow;
    }
    setSearchedProducts(structuredMockups);
    console.log(structuredMockups);

    setLoading(false);
  };
  var getMockups = () => {
    getDesigns();
    setLoading(true);
    try {
      axios.get(config.host + "/api/mocks/GetAllMocks/").then((response) => {
        var rawMockups = response.data;
        console.log(rawMockups);

        setRawMockups(rawMockups);
        structureMockups(rawMockups);
      });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var deleteProducts = () => {
    for (var i = 0; i < rawProductsState.length; i = i + 1) {
      try {
        axios
          .delete(
            config.host +
              "/api/mocks/deleteProduct/?productID=" +
              rawProductsState[i].ArtistProductID
          )
          .then((response) => {});
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    }
  };

  var updateProduct = () => {
    console.log(editProduct);
    setLoading(true);
    try {
      console.log({
        angle: angle,
        wraping: wrapping,
        scaled_width: parseInt(0.00008 * editProduct.mockWidth * hyp),
        scaled_height: parseInt(0.00008 * editProduct.mockHeight * hyp),
        width: itemWidth,
        height: itemHeight,
        posx: coords.x,
        posy: coords.y,
        reduction: reduction,
        pattern: pattern,
      });
      axios
        .patch(
          config.host +
            "/api/mocks/updateProduct/?productID=" +
            editProduct.ArtistProductID,
          {
            angle: angle,
            wraping: wrapping,
            scaled_width: parseInt(0.00008 * editProduct.mockWidth * hyp),
            scaled_height: parseInt(0.00008 * editProduct.mockHeight * hyp),
            width: itemWidth,
            height: itemHeight,
            posx: coords.x,
            posy: coords.y,
            reduction: reduction,
            pattern: pattern,
          }
        )
        .then((response) => {
          setPreviewImage({
            uri: response.data[0].productPhoto + "?" + new Date(),
          });
          setMockupImage({
            uri: response.data[0].mockPhoto,
          });
          setLoading(false);
          setEditProduct(response.data[0]);

          var productIndex = rawProductsState.findIndex(
            (element) => element.ArtistProductID == editProduct.ArtistProductID
          );
          console.log(response.data[0]);

          var updatedProducts = rawProductsState;
          console.log(productIndex);
          console.log("updated product", response.data[0]);
          updatedProducts[productIndex].productPhoto =
            editProduct.productPhoto + "?" + new Date();
          setRawProducts(updatedProducts);

          setEditProduct(response.data[0]);

          setPreviewImage({
            uri: response.data[0].productPhoto + "?" + new Date(),
          });

          updateProducts();
          // structureProducts(response.data);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var saveAsDraft = () => {
    setLoading(true);
    try {
      axios
        .patch(
          config.host +
            "/api/mocks/setProductStatus/?productID=" +
            editProduct.ArtistProductID,
          {
            productStatus: "D",
          }
        )
        .then((response) => {
          setLoading(false);
          updateProducts();
          setIsview(false);
        });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  var requestApproval = () => {
    try {
      axios
        .patch(
          config.host +
            "/api/mocks/setProductStatus/?productID=" +
            editProduct.ArtistProductID,
          {
            productStatus: "PE",
          }
        )
        .then((response) => {
          setLoading(false);
          updateProducts();
          setIsview(false);
        });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };
  useEffect(getMockups, [search, selectedCategory]);
  useEffect(() => {
    structureDesigns(rawDesignsState);
  }, [designSearch]);

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          borderRadius: 20,
          backgroundColor: "#FFFFFF",
        }}
      >
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            setIsview(false);
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "97%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Edit product
              </Text>
            </View>

            <View
              style={{
                height: "75%",
                width: "90%",
                borderWidth: 1,
                borderColor: "#C4C4C4",
                marginTop: "1%",
                borderRadius: 4,
                alignItems: "center",
              }}
            >
              <View
                style={{
                  flexDirection: "row",
                  height: "3%",
                  width: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                  marginBottom: "1%",
                  marginTop: "1%",
                }}
              ></View>

              <View
                style={{
                  height: "92%",
                  width: "100%",
                  borderTopWidth: 1,
                  borderColor: "#C4C4C4",
                  flexDirection: "row",

                  zIndex: -1,
                }}
              >
                {/* <View
                  style={{
                    width: windowWidth * 0.12,
                    height: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    backgroundColor: "#3D9EBC88",
                  }}
                >
                  <ScrollView
                    style={{
                      width: "100%",
                      height: "100%",
                    }}
                  >
                    {rawProductsState.map((product) => (
                      <View
                        style={{
                          height: windowWidth * 0.125,
                          width: "100%",
                        }}
                        key={product.ArtistProductID}
                      >
                        <View
                          style={{
                            height: windowWidth * 0.0125,
                            width: "100%",
                          }}
                        />
                        <View
                          style={{
                            height: windowWidth * 0.1,
                            width: "100%",
                            alignSelf: "center",
                          }}
                        >
                          <TouchableOpacity
                            style={{
                              width: windowWidth * 0.1,
                              height: "auto",
                              borderRadius: 15,
                              alignItems: "center",
                              justifyContent: "center",
                              shadowRadius: 20,
                              shadowColor: "#C4C4C4",
                              alignSelf: "center",
                              backgroundColor: "white",
                              borderWidth: editProduct == product ? 1 : 0,
                            }}
                            key={product.ArtistProductID}
                            onPress={() => {
                              console.log(product);
                              setEditProduct(product);
                              setPreviewImage({
                                uri: product.productPhoto,
                              });
                              setMockupImage({
                                uri: product.mockPhoto,
                              });
                            }}
                          >
                            <View style={{ height: "80%", width: "100%" }}>
                              <Image
                                source={
                                  product.productPhoto == ""
                                    ? placeholder
                                    : {
                                        uri: product.productPhoto,
                                      }
                                }
                                style={{
                                  width: windowWidth * 0.085,
                                  height: windowWidth * 0.085,
                                  resizeMode: "contain",
                                  borderRadius: 15,
                                }}
                              />
                            </View>
                            <View
                              style={{
                                height: "20%",
                                width: "100%",
                                backgroundColor: "#FFFFFF",
                                alignItems: "center",
                                justifyContent: "center",
                                borderBottomLeftRadius: 15,
                                borderBottomRightRadius: 15,
                              }}
                            >
                              <Text
                                style={{
                                  fontFamily: "Roboto_700Bold",
                                  fontSize: windowHeight <= 1080 ? 14 : 18,
                                  color: "#333333",
                                }}
                              >
                                {product.productName}
                              </Text>
                            </View>
                          </TouchableOpacity>
                        </View>

                        <View
                          style={{
                            height: windowWidth * 0.0125,
                            width: "100%",
                          }}
                        />
                      </View>
                    ))}
                  </ScrollView>
                </View> */}

                {editProduct ? (
                  <View
                    style={{
                      height: "80%",
                      width: "90%",
                      justifyContent: "center",
                      flexDirection: "row",
                      alignItems: "center",
                    }}
                  >
                    {editProduct.productStatus == "D" ||
                    editProduct.productStatus == "PE" ? (
                      editProduct.sub_category == "Tshirt" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "90%",
                            alignItems: "center",
                            justifyContent: "center",
                            alignSelf: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "100%",
                              height: "100%",
                              alignItems: "center",
                              justifyContent: "center",
                              flexDirection: "row",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: 0.00008 * editProduct.mockWidth * hyp,
                                height: 0.00008 * editProduct.mockHeight * hyp,
                                alignItems: "center",
                                justifyContent: "center",
                                marginTop: "2%",
                                zIndex: 999,
                              }}
                              onPress={(evt) => {
                                handleMouseMove(evt);
                              }}
                            >
                              <View
                                style={{
                                  position: "absolute",
                                  top: coords.y - 5,
                                  left: coords.x - 5,
                                  backgroundColor: "red",
                                  width: 10,
                                  height: 10,
                                  borderRadius: 50,
                                }}
                              ></View>
                              <View
                                style={{
                                  position: "absolute",
                                  top: coords.y - itemHeight / 2,
                                  left: coords.x - itemWidth / 2,
                                  backgroundColor: "#888888AA",
                                  width: itemWidth,
                                  height: itemHeight,

                                  zIndex: 0,
                                }}
                              ></View>
                              <Image
                                source={
                                  mockupImage == null
                                    ? { uri: editProduct.mockPhoto }
                                    : mockupImage
                                }
                                style={{
                                  width: 0.00008 * editProduct.mockWidth * hyp,
                                  height:
                                    0.00008 * editProduct.mockHeight * hyp,
                                  resizeMode: "contain",
                                  zIndex: -1,
                                }}
                              />
                              <Text
                                style={{
                                  fontFamily: "Roboto_700Bold",
                                  fontSize: windowHeight * 0.014,
                                  color: "#333333",
                                  zIndex: 0,
                                }}
                              >
                                {coords.x}, {coords.y}
                              </Text>

                              {/* <View
                                        style={{ position: "absolute", zIndex: 999 }}
                                      >
                                        <FlatItem
                                          item={data[0]}
                                          itemHeight={itemHeight}
                                          itemWidth={itemWidth}
                                          maxLimitY={869 / 2}
                                          minLimitY={-869 / 2}
                                          maxLimitX={670 / 2}
                                          minLimitX={-670 / 2}
                                          posxHanlder={setPosx}
                                          posyHanlder={setPosy}
                                        />
                                      </View> */}
                            </TouchableOpacity>

                            <View
                              style={{
                                width: "50%",
                                height: "50%",
                                alignItems: "center",
                                justifyContent: "center",
                              }}
                            >
                              <View
                                style={{
                                  width: "100%",
                                  height: "2%",
                                  alignItems: "center",
                                  marginTop: "1%",
                                  marginBottom: "2%",
                                  justifyContent: "center",
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight * 0.014,
                                    color: "#333333",
                                  }}
                                >
                                  Art height
                                </Text>
                              </View>

                              <Slider
                                style={{
                                  width: "50%",
                                  height: "1%",
                                  zIndex: -1,
                                }}
                                minimumValue={10}
                                maximumValue={0.2 * windowHeight}
                                minimumTrackTintColor="#3D9EBC"
                                maximumTrackTintColor="#000000"
                                thumbTintColor="#EB5276"
                                value={itemHeight}
                                onValueChange={(value) => {
                                  setItemHeight(parseInt(value));
                                }}
                              />

                              <View
                                style={{
                                  width: "100%",
                                  height: "2%",
                                  alignItems: "center",
                                  marginBottom: "2%",
                                  justifyContent: "center",
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight * 0.014,
                                    color: "#333333",
                                  }}
                                >
                                  Art width
                                </Text>
                              </View>
                              <Slider
                                style={{
                                  width: "50%",
                                  height: "1%",
                                  zIndex: -1,
                                }}
                                minimumValue={10}
                                maximumValue={800}
                                minimumTrackTintColor="#3D9EBC"
                                maximumTrackTintColor="#000000"
                                thumbTintColor="#EB5276"
                                value={itemWidth}
                                onValueChange={(value) => {
                                  console.log(value);
                                  setItemWidth(parseInt(value));
                                }}
                              />
                            </View>
                          </View>
                          <TouchableOpacity
                            style={{
                              width: "40%",
                              alignSelf: "center",
                              height: "8%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "8%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : editProduct.sub_category == "Mug" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "60%",
                            alignItems: "center",
                            justifyContent: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "100%",
                              height: "1%",
                              alignItems: "center",
                              marginTop: "1%",
                              marginBottom: "6%",
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                fontSize: windowHeight * 0.015,
                                color: "#333333",
                              }}
                            >
                              Wrapping Percentage
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Roboto_500Medium",
                                fontSize: windowHeight * 0.013,
                                color: "#EB5276",
                                backgroundColor: "#AAAAAAAA",
                                borderRadius: 50,
                                marginTop: "1%",
                                padding: 10,
                                marginBottom: 10,
                              }}
                            >
                              {parseInt(wrapping)}%
                            </Text>
                          </View>

                          <Slider
                            style={{ width: "50%", height: "1%" }}
                            minimumValue={10}
                            maximumValue={100}
                            minimumTrackTintColor="#3D9EBC"
                            maximumTrackTintColor="#000000"
                            thumbTintColor="#EB5276"
                            value={wrapping}
                            onValueChange={(value) => {
                              console.log(value);
                              setWrapping(parseInt(value).toString());
                            }}
                          />

                          <View
                            style={{
                              width: "100%",
                              height: "1%",
                              alignItems: "center",
                              marginBottom: "6%",
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                fontSize: windowHeight * 0.015,
                                color: "#333333",
                              }}
                            >
                              Rotation Angle
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Roboto_500Medium",
                                fontSize: windowHeight * 0.013,
                                color: "#EB5276",
                                backgroundColor: "#AAAAAAAA",
                                borderRadius: 50,
                                marginTop: "1%",
                                padding: 10,
                              }}
                            >
                              {parseInt(angle)}
                            </Text>
                          </View>
                          <Slider
                            style={{ width: "50%", height: "1%" }}
                            minimumValue={0}
                            maximumValue={360}
                            minimumTrackTintColor="#3D9EBC"
                            maximumTrackTintColor="#000000"
                            thumbTintColor="#EB5276"
                            value={angle}
                            onValueChange={(value) => {
                              console.log(value);
                              setAngle(parseInt(value).toString());
                            }}
                          />
                          <TouchableOpacity
                            style={{
                              width: "20%",
                              alignSelf: "center",
                              height: "10%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "5%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : editProduct.sub_category == "Phone_Case" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "100%",
                            alignItems: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "30%",
                              height: "10%",
                              borderRadius: 50,
                              flexDirection: "row",
                              borderWidth: 1,
                              alignItems: "center",
                              justifyContent: "center",
                              marginBottom: "1%",
                              marginTop: "1%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                height: "100%",
                                width: "50%",
                                borderTopLeftRadius: 50,
                                borderBottomLeftRadius: 50,
                                backgroundColor:
                                  pattern == "t" ? "#EB5276" : "#FFFFFF",

                                alignItems: "center",
                                justifyContent: "center",
                              }}
                              onPress={() => {
                                setPattern("t");
                              }}
                            >
                              <Text
                                style={{
                                  fontFamily: "Roboto_700Bold",
                                  fontSize: windowHeight * 0.015,
                                  color: "#333333",
                                }}
                              >
                                Pattern
                              </Text>
                            </TouchableOpacity>
                            <TouchableOpacity
                              style={{
                                height: "100%",
                                width: "50%",
                                borderTopRightRadius: 50,
                                borderBottomRightRadius: 50,
                                backgroundColor:
                                  pattern == "" ? "#EB5276" : "#FFFFFF",

                                alignItems: "center",
                                justifyContent: "center",
                              }}
                              onPress={() => {
                                setPattern("");
                              }}
                            >
                              <Text
                                style={{
                                  fontFamily: "Roboto_700Bold",
                                  fontSize: windowHeight * 0.015,
                                  color: "#333333",
                                }}
                              >
                                Solid
                              </Text>
                            </TouchableOpacity>
                          </View>
                          {pattern == "t" ? (
                            <View
                              style={{
                                height: "50%",
                                width: "50%",
                                alignItems: "center",
                                justifyContent: "center",
                              }}
                            >
                              <View
                                style={{
                                  width: "100%",
                                  height: "1%",
                                  alignItems: "center",
                                  marginTop: "1%",
                                  marginBottom: "2%",
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight * 0.015,
                                    color: "#333333",
                                  }}
                                >
                                  Reduction Percentage
                                </Text>
                                <Text
                                  style={{
                                    fontFamily: "Roboto_500Medium",
                                    fontSize: windowHeight * 0.013,
                                    color: "#EB5276",
                                    backgroundColor: "#AAAAAAAA",
                                    borderRadius: 50,
                                    marginTop: "1%",
                                    padding: 10,
                                    marginBottom: 10,
                                  }}
                                >
                                  {parseInt(reduction)}%
                                </Text>
                              </View>
                              <Slider
                                style={{ width: "50%", height: "1%" }}
                                minimumValue={1}
                                maximumValue={100}
                                minimumTrackTintColor="#3D9EBC"
                                maximumTrackTintColor="#000000"
                                thumbTintColor="#EB5276"
                                value={wrapping}
                                onValueChange={(value) => {
                                  console.log(value);
                                  setReduction(parseInt(value).toString());
                                }}
                              />
                            </View>
                          ) : (
                            <View
                              style={{
                                width: "100%",
                                height: "80%",
                                alignItems: "center",

                                marginBottom: "2%",
                                flexDirection: "row",
                                justifyContent: "center",
                              }}
                            >
                              <TouchableOpacity
                                style={{
                                  width: parseInt(
                                    0.0002 * editProduct.mockWidth * hyp
                                  ),
                                  height: parseInt(
                                    0.0002 * editProduct.mockHeight * hyp
                                  ),
                                  alignItems: "center",
                                  justifyContent: "center",
                                  marginTop: "2%",
                                }}
                                onPress={handleMouseMove}
                              >
                                <View
                                  style={{
                                    position: "absolute",
                                    top: coords.y - 5,
                                    left: coords.x - 5,
                                    backgroundColor: "red",
                                    width: 10,
                                    height: 10,
                                    borderRadius: 50,
                                    zIndex: 999,
                                  }}
                                ></View>
                                <View
                                  style={{
                                    position: "absolute",
                                    top: coords.y - itemHeight / 2,
                                    left: coords.x - itemWidth / 2,
                                    backgroundColor: "#888888AA",
                                    width: itemWidth,
                                    height: itemHeight,

                                    zIndex: 0,
                                  }}
                                ></View>
                                <Image
                                  source={phone_case}
                                  style={{
                                    width: parseInt(
                                      0.0002 * editProduct.mockWidth * hyp
                                    ),
                                    height: parseInt(
                                      0.0002 * editProduct.mockHeight * hyp
                                    ),
                                    resizeMode: "contain",
                                    zIndex: -1,
                                  }}
                                />
                                {/* <View
                                          style={{ position: "absolute", zIndex: 999 }}
                                        >
                                          <FlatItem
                                            item={data[0]}
                                            itemHeight={itemHeight}
                                            setItemHeight={setItemHeight}
                                            itemWidth={itemWidth}
                                            setItemWidth={setItemWidth}
                                            maxLimitY={1000 / 2}
                                            minLimitY={-1000 / 2}
                                            maxLimitX={1000 / 2}
                                            minLimitX={-1000 / 2}
                                            posxHanlder={setPosx}
                                            posyHanlder={setPosy}
                                          />
                                        </View> */}
                              </TouchableOpacity>

                              <View
                                style={{
                                  width: "50%",
                                  height: "50%",
                                  alignItems: "center",
                                  justifyContent: "center",
                                }}
                              >
                                <View
                                  style={{
                                    width: "100%",
                                    height: "2%",
                                    alignItems: "center",
                                    marginTop: "1%",
                                    marginBottom: "2%",
                                    justifyContent: "center",
                                  }}
                                >
                                  <Text
                                    style={{
                                      fontFamily: "Roboto_700Bold",
                                      fontSize: windowHeight * 0.014,
                                      color: "#333333",
                                    }}
                                  >
                                    Art height
                                  </Text>
                                </View>

                                <Slider
                                  style={{
                                    width: "50%",
                                    height: "1%",
                                    zIndex: -1,
                                  }}
                                  minimumValue={10}
                                  maximumValue={1000}
                                  minimumTrackTintColor="#3D9EBC"
                                  maximumTrackTintColor="#000000"
                                  thumbTintColor="#EB5276"
                                  value={itemHeight}
                                  onValueChange={(value) => {
                                    setItemHeight(parseInt(value));
                                  }}
                                />

                                <View
                                  style={{
                                    width: "100%",
                                    height: "2%",
                                    alignItems: "center",
                                    marginBottom: "2%",
                                    justifyContent: "center",
                                  }}
                                >
                                  <Text
                                    style={{
                                      fontFamily: "Roboto_700Bold",
                                      fontSize: windowHeight * 0.014,
                                      color: "#333333",
                                    }}
                                  >
                                    Art width
                                  </Text>
                                </View>
                                <Slider
                                  style={{
                                    width: "50%",
                                    height: "1%",
                                    zIndex: -1,
                                  }}
                                  minimumValue={10}
                                  maximumValue={1000}
                                  minimumTrackTintColor="#3D9EBC"
                                  maximumTrackTintColor="#000000"
                                  thumbTintColor="#EB5276"
                                  value={wrapping}
                                  onValueChange={(value) => {
                                    console.log(value);
                                    setItemWidth(parseInt(value));
                                  }}
                                />
                              </View>
                            </View>
                          )}
                          <TouchableOpacity
                            style={{
                              width: "40%",
                              alignSelf: "center",
                              height: "10%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "5%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : (
                        <View />
                      )
                    ) : (
                      <View />
                    )}

                    <View
                      style={{
                        width: "50%",
                        height: "100%",
                        alignItems: "center",

                        justifyContent: "center",
                      }}
                    >
                      <Image
                        source={
                          previewImage == null
                            ? {
                                uri: editProduct.productPhoto,
                              }
                            : previewImage
                        }
                        style={{
                          width: "100%",
                          height: "100%",
                          resizeMode: "contain",
                          borderRadius: 15,
                        }}
                      />
                    </View>
                  </View>
                ) : (
                  <View
                    style={{
                      height: "90%",
                      width: "80%",
                      alignItems: "center",
                      justifyContent: "center",
                      marginTop: "2%",
                    }}
                  >
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 20 : 32,
                        color: "#C54160",
                      }}
                    >
                      Choose a product to view
                    </Text>
                    <Image
                      source={no_products}
                      style={{
                        marginTop: "5%",
                        width: "40%",
                        height: "40%",
                        resizeMode: "contain",
                        alignSelf: "center",
                      }}
                    />
                  </View>
                )}
              </View>
            </View>

            <View style={{ height: "1%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />

              <TouchableOpacity style={styles.button2} onPress={saveAsDraft}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#333333",
                  }}
                >
                  Save as draft
                </Text>
              </TouchableOpacity>

              <View
                style={{
                  width: "50%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 20,
                    color: "#333333",
                  }}
                >
                  Edit product
                </Text>
              </View>

              <TouchableOpacity style={styles.button} onPress={requestApproval}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#FFFFFF",
                  }}
                >
                  Request approval to publish
                </Text>
              </TouchableOpacity>

              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default EditProductMain;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,

    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,

    backgroundColor: "#FFFFFF",
    borderColor: "black",
    borderWidth: 1,
    zIndex: 999,
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "80%",
    height: "18%",
    padding: 8,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
