import {
  Animated,
  StyleSheet,
  PixelRatio,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import { slideOutLeft } from "react-animations";
import Radium, { StyleRoot } from "radium";
import axios from "axios";

import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
import ScaledImage from "../utils/ScaledImage";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.svg");
var info1 = require("../../assets/money-info-1.svg");
var info2 = require("../../assets/money-info-2.svg");
var demo = require("../../assets/demo.png");

var Home = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const loginHandler = () => {
    navigation.navigate("Kairo Merch Login");
  };
  const signupHandler = () => {
    navigation.navigate("Kairo Merch Signup");
  };
  useEffect(() => {
    var cookies = document.cookie.split(";");
    console.log(cookies);

    for (let i = 0; i < cookies.length; i = i + 1) {
      if (cookies[i].split("=")[0].includes("config")) {
        var configCookies = JSON.parse(cookies[i].split("=")[1]);
        if (configCookies.token != "") {
          try {
            axios
              .get(
                config.host +
                  "/api/account/getAccountStatus?token=" +
                  configCookies.token
              )
              .then((response) => {
                //Navigate to verification page
                console.log(response);
                config.status = response.data.account_status;
                config.verified = response.data.mobile_verification;
                config.approved = response.data.account_approval;
                config.token = configCookies.token;
                config.plan = configCookies.plan;
                if (config.verified) {
                  if (config.status == 0) {
                    navigation.navigate("Kairo Merch Complete Profile");
                  } else if (!config.approved) {
                    navigation.navigate("Kairo Merch Shop Setup");
                  } else if (config.status == 4) {
                    navigation.navigate("Kairo Merch Shop");
                  } else {
                    navigation.navigate("Kairo Merch Advisor", {
                      status: config.status,
                    });
                  }
                } else {
                  navigation.navigate("Kairo Merch Mobile Verification");
                }
              })
              .catch((e) => {
                navigation.navigate("Kairo Merch Complete Profile");
              });
          } catch (e) {
            var err = "";
            for (let x in JSON.parse(e.request.response)) {
              console.log(e.request.response.x);
              err = err + JSON.parse(e.request.response)[x] + "\n";
            }
          }
        }

        console.log(configCookies);
      }
    }
  }, []);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: windowWidth,
          height: windowHeight,
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "12%",
            width: "100%",
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>

          <Image
            source={logo}
            style={{ width: "15%", height: "80%" }}
            resizeMode={"contain"}
          />
        </View>
        <View style={{ height: "3%" }}></View>

        <View style={{ height: "75%", width: "100%" }}>
          <View
            style={{
              width: "100%",
              flexDirection: "row",
              justifyContent: "space-between",
              height: "8%",
            }}
          >
            <View style={{ width: "80%" }}>
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: 36 * (windowHeight / 900),
                  color: "#3D9EBC",
                  marginLeft: "2%",
                  marginTop: "1%",
                }}
              >
                Welcome to KairoMerch!
              </Text>
            </View>

            <View
              style={{
                flexDirection: "row",
                width: "20%",
                height: "100%",
                justifyContent: "space-evenly",
              }}
            >
              <TouchableOpacity style={styles.button2} onPress={loginHandler}>
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: 16 * (windowHeight / 900),
                    color: "#FFFFFF",
                  }}
                >
                  Login
                </Text>
              </TouchableOpacity>
              <View style={{ width: "1%" }} />
              <TouchableOpacity style={styles.button2} onPress={signupHandler}>
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: 16 * (windowHeight / 900),
                    color: "#FFFFFF",
                  }}
                >
                  Sign up
                </Text>
              </TouchableOpacity>
              <View style={{ width: "2%" }} />
            </View>
            <View style={{ width: "1%" }} />
          </View>

          <View style={{ flexDirection: "row", width: "100%", height: "85%" }}>
            <View style={{ width: "60%", height: "100%" }}>
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: 24 * (windowHeight / 900),
                  color: "#333333",
                  marginLeft: "4%",
                  marginTop: "2%",
                }}
              >
                Hello Artist, we would love to have you onboard.
              </Text>
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: 16 * (windowHeight / 900),
                  color: "#000000B3",
                  marginLeft: "4%",
                  marginTop: "2%",
                }}
              >
                Kairo Merch is the fastest solution for independent artists to
                launch an online shop and sell their creative art through online
                products.
              </Text>
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: 24 * (windowHeight / 900),
                  color: "#C54160",
                  marginLeft: "6%",
                  marginTop: "4%",
                  marginBottom: "2%",
                }}
              >
                Join us now and gain cash effortlessly!
              </Text>

              <View style={{ height: "10%" }}></View>

              <View
                style={{
                  width: "70%",

                  height: "50%",
                  flexDirection: "row",
                  marginLeft: "6%",
                  marginTop: "1%",
                }}
              >
                <View style={{ alignItems: "center", width: "50%" }}>
                  <Image
                    source={info1}
                    style={{
                      width: "100%",
                      height: "60%",
                      resizeMode: "center",
                    }}
                  />
                  <View
                    style={{
                      flexDirection: "row",
                      marginTop: "5%",
                    }}
                  >
                    <View
                      style={{
                        width: "100%",
                        alignItems: "center",
                      }}
                    >
                      <Text
                        style={{
                          fontFamily: "Roboto_500Medium",
                          fontSize: 18 * (windowHeight / 900),
                          color: "#04475C",
                          width: "80%",
                          marginTop: "5%",
                        }}
                      >
                        Easily upload your art to a wide range of high quality
                        products
                      </Text>
                    </View>
                  </View>
                </View>
                <View style={{ alignItems: "center", width: "50%" }}>
                  <Image
                    source={info2}
                    style={{
                      width: "100%",
                      height: "60%",
                      resizeMode: "center",
                    }}
                  />
                  <View
                    style={{
                      width: "100%",
                      alignItems: "center",
                    }}
                  >
                    <Text
                      style={{
                        fontFamily: "Roboto_500Medium",
                        fontSize: 18 * (windowHeight / 900),
                        color: "#04475C",
                        marginTop: "5%",
                        width: "80%",
                      }}
                    >
                      Customers order the products they love featuring your art
                      and we handle the orders fulfillment and delivery!
                    </Text>
                  </View>
                </View>
              </View>
            </View>
            <View style={{ width: "5%" }} />
            <View style={{ width: "30%", height: "100%" }}>
              <Image
                source={demo}
                style={{
                  width: "100%",
                  height: "100%",
                  resizeMode: "contain",
                }}
              />
            </View>
          </View>

          <View style={{ marginLeft: 65, width: "30%" }}></View>
        </View>
        <TouchableOpacity style={styles.button} onPress={signupHandler}>
          <Text
            style={{
              fontFamily: "Roboto_400Regular",
              fontSize: windowHeight <= 1080 ? 16 : 20,
              color: "#FFFFFF",
            }}
          >
            Start now
          </Text>
        </TouchableOpacity>
      </View>
    );
  }
};

export default Home;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 10,
    width: "20%",
    height: 0.04 * windowHeight,

    backgroundColor: "#F7567C",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "40%",
    height: "80%",
    padding: 18,

    backgroundColor: "#3D9EBC",
  },
});
